*,
*::before,
*::after {
  box-sizing: border-box;
  color: inherit;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: @desktop-width - 128px;
  margin-right: auto;
  margin-left: auto;
}

.title() {
  font-weight: 700;
  font-size: 20px;
  margin: 0;
  margin-bottom: 16px;
}

.circle {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 48px;
  height: 48px;
  border: none;
  border-radius: 50%;
  background-color: @primary;
  box-shadow: 4px 4px 40px fade(@primary, 60%);
}

.video {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.4;
  font-weight: 700;
  text-align: right;

  &::after {
    content: "";
    display: block;
    min-width: 48px;
    height: 48px;
    border-radius: 50%;
    margin-left: 16px;
    box-shadow: 4px 4px 40px fade(@primary, 60%);
    background-color: @primary;
    background-repeat: no-repeat;
    background-position: 19px center;
    background-image: url("data:image/svg+xml,%3Csvg width='15' height='20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 1.838a1 1 0 0 1 1.543-.84L14.165 9.16a1 1 0 0 1 0 1.68L1.543 19.002A1 1 0 0 1 0 18.162V1.838Z' fill='%23fff'/%3E%3C/svg%3E");
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: @primary;
  border: none;
  border-radius: 4px;
  color: @basic-white;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.4;
  width: max-content;
  padding: 16px 24px;
  box-shadow: 4px 4px 40px fade(@primary, 60%);
  cursor: pointer;

  &:hover {
    box-shadow: none;
  }

  &::before {
    content: "";
    display: block;
    min-width: 20px;
    height: 20px;
    margin-right: 8px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 17.333A8.333 8.333 0 1 0 9 .667a8.333 8.333 0 0 0 0 16.666Z' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M9 12.333 12.333 9 9 5.667M5.667 9h6.666' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
  }
}
