.carrier-link {
  position: relative;
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 264px;
  height: 48px;
  font-style: 14px;
  font-weight: 700;
  border: 2px solid @basic-white;
  border-radius: 4px;

  &--current {
    background-color: @primary;
    border-radius: 4px;
    font-weight: 400;
    border: none;
  }

  &--current::after {
    content: "";
    position: absolute;
    left: 4px;
    top: 4px;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: @primary;
    filter: blur(28px);
  }
}

.carrier-link__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  margin-right: 8px;
}
