*,
*::before,
*::after {
  box-sizing: border-box;
  color: inherit;
}
img {
  max-width: 100%;
  display: block;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 1152px;
  margin-right: auto;
  margin-left: auto;
}
.circle {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 48px;
  height: 48px;
  border: none;
  border-radius: 50%;
  background-color: #ed2e38;
  box-shadow: 4px 4px 40px rgba(237, 46, 56, 0.6);
}
.video {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.4;
  font-weight: 700;
  text-align: right;
}
.video::after {
  content: "";
  display: block;
  min-width: 48px;
  height: 48px;
  border-radius: 50%;
  margin-left: 16px;
  box-shadow: 4px 4px 40px rgba(237, 46, 56, 0.6);
  background-color: #ed2e38;
  background-repeat: no-repeat;
  background-position: 19px center;
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 1.838a1 1 0 0 1 1.543-.84L14.165 9.16a1 1 0 0 1 0 1.68L1.543 19.002A1 1 0 0 1 0 18.162V1.838Z' fill='%23fff'/%3E%3C/svg%3E");
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ed2e38;
  border: none;
  border-radius: 4px;
  color: #fff;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.4;
  width: max-content;
  padding: 16px 24px;
  box-shadow: 4px 4px 40px rgba(237, 46, 56, 0.6);
  cursor: pointer;
}
.button:hover {
  box-shadow: none;
}
.button::before {
  content: "";
  display: block;
  min-width: 20px;
  height: 20px;
  margin-right: 8px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 17.333A8.333 8.333 0 1 0 9 .667a8.333 8.333 0 0 0 0 16.666Z' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M9 12.333 12.333 9 9 5.667M5.667 9h6.666' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
.page {
  height: 100%;
  font-size: 16px;
  line-height: 1.4;
  color: #1a1a1a;
  font-family: "DIN Pro", "Arial", sans-serif;
  overflow-x: hidden;
}
.page::-webkit-scrollbar {
  width: 8px;
}
.page::-webkit-scrollbar-track {
  background-color: #ed2e38;
}
.page::-webkit-scrollbar-thumb {
  background-color: #3c324c;
}
.page__body {
  min-height: 100%;
  background-color: #fff;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
.social-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}
.social-list__item:not(:last-child) {
  margin-right: 8px;
}
.social-list__link {
  display: flex;
  align-items: center;
}
.social-list__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  margin-right: 8px;
}
.details {
  display: flex;
  align-items: center;
}
.details__item {
  display: flex;
  align-items: center;
}
.details__item:not(:last-child) {
  margin-right: 32px;
}
.details__icon {
  display: flex;
  width: 20px;
  height: 20px;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
}
.locales {
  display: flex;
  align-items: center;
}
.locales__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  margin-right: 8px;
}
.locales__list {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
}
.locales__item:not(:last-child) {
  margin-right: 8px;
}
.locales__item--current {
  color: #8775a4;
}
.locales__link {
  display: block;
}
.page-nav {
  padding-top: 28px;
  padding-bottom: 28px;
  background-color: #3c324c;
  color: #fff;
}
.page-nav__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page-nav__list {
  position: relative;
  display: flex;
  padding: 0;
  margin: 0;
  height: 40px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  color: #8775a4;
}
.page-nav__item {
  position: relative;
  display: flex;
  align-items: flex-end;
}
.page-nav__item:not(:last-child) {
  margin-right: 32px;
}
.page-nav__item--current {
  color: #fff;
}
.page-nav__item--current::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(-50%);
  width: 100%;
  height: 5px;
  background-color: #ed2e38;
  border-radius: 5px;
}
.page-nav__link {
  display: block;
  min-width: max-content;
}
.page-nav__button {
  display: block;
  border: none;
  background-color: transparent;
  padding: 0;
  min-width: max-content;
  cursor: pointer;
}
.page-nav__icon {
  color: #ed2e38;
  position: absolute;
  top: calc(100% + 10px);
  left: 10px;
}
.page-nav__sublist {
  position: absolute;
  left: -10px;
  top: calc(100% + 23px);
  list-style: none;
  padding: 32px;
  margin: 0;
  color: #fff;
  background-color: #8775a4;
  border-radius: 4px;
  box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 65px 47px rgba(0, 0, 0, 0.05), 0px 39px 25px rgba(0, 0, 0, 0.04), 0px 20px 13px rgba(0, 0, 0, 0.035), 0px 8px 7px rgba(0, 0, 0, 0.027), 0px 2px 3px rgba(0, 0, 0, 0.017);
}
.page-nav__sublist--hidden {
  display: none;
}
.page-nav__sublist--hidden + .page-nav__icon {
  display: none;
}
.page-nav__subitem:not(:last-child) {
  margin-bottom: 8px;
}
.page-nav__sublink {
  display: block;
  min-width: max-content;
}
.carrier-link {
  position: relative;
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 264px;
  height: 48px;
  font-style: 14px;
  font-weight: 700;
  border: 2px solid #fff;
  border-radius: 4px;
}
.carrier-link--current {
  background-color: #ed2e38;
  border-radius: 4px;
  font-weight: 400;
  border: none;
}
.carrier-link--current::after {
  content: "";
  position: absolute;
  left: 4px;
  top: 4px;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #ed2e38;
  filter: blur(28px);
}
.carrier-link__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  margin-right: 8px;
}
.page-header {
  background-color: #fff;
  z-index: 1;
}
.page-header__top {
  display: flex;
  align-items: center;
  padding-top: 64px;
  padding-bottom: 32px;
}
.page-header__logo {
  display: block;
  margin-right: 64px;
}
.page-header__details {
  margin-right: 64px;
}
.page-header__social-list {
  margin-left: auto;
  margin-right: 104px;
}
.page-footer {
  background-color: #fff;
}
.page-footer__top {
  position: relative;
  background-color: #3c324c;
  display: flex;
  justify-content: center;
  color: #fff;
  padding-top: 64px;
  padding-bottom: 64px;
}
.page-footer__menu {
  display: flex;
  margin: 0;
}
.page-footer__menu-list:not(:last-child) {
  margin-right: 64px;
}
.page-footer__menu-caption {
  font-weight: 700;
  font-size: 20px;
  margin: 0;
  margin-bottom: 16px;
}
.page-footer__menu-item {
  margin-left: 0;
  margin-bottom: 8px;
}
.page-footer__button {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(calc(1280px / 2 - 64px - 48px)) rotate(-90deg);
  cursor: pointer;
}
.page-footer__button:hover {
  box-shadow: none;
}
.page-footer__bottom {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
  padding-top: 32px;
  padding-bottom: 32px;
}
.page-footer__details {
  margin-right: 32px;
}
.page-footer__copyright {
  width: 100%;
  text-align: center;
  margin-top: 32px;
  text-transform: uppercase;
}
.board {
  position: relative;
  z-index: 0;
  height: 540px;
  background-color: lightgrey;
  color: #fff;
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.board__container {
  padding: 80px 112px;
}
.board__mask {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.main-page__board [data-content] {
  max-width: 406px;
}
.main-page__board [data-content] h1,
.main-page__board [data-content] h2 {
  font-weight: 700;
  font-size: 50px;
  margin-top: 0;
  margin-bottom: 16px;
}
.main-page__board [data-content] p {
  font-weight: 300;
  font-size: 28px;
}
.main-page__board [data-content] a {
  margin-top: 32px;
  min-width: 220px;
}
.main-page__grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-auto-rows: 320px;
  gap: 32px;
  margin-top: 32px;
  margin-bottom: 32px;
}
.main-page__grid-item {
  position: relative;
  z-index: 0;
  grid-column: span 2;
  padding: 48px;
  display: flex;
  align-items: center;
  border-radius: 4px;
  color: #fff;
  overflow: hidden;
}
.main-page__grid-item::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(60, 50, 76, 0.85), rgba(60, 50, 76, 0.85));
}
.main-page__grid-item [data-content] h2 {
  font-weight: 700;
  font-size: 20px;
  margin: 0;
  margin-bottom: 16px;
}
.main-page__grid-item [data-content] a {
  margin-top: 32px;
}
.main-page__grid-item:first-child,
.main-page__grid-item:nth-child(2) {
  grid-column: span 3;
}
.main-page__grid-item:nth-last-child(2) {
  order: 1;
}
.main-page__grid-item:last-child {
  grid-column: span 4;
  grid-row: span 2;
}
.main-page__grid-item:last-child [data-content] {
  width: 360px;
  margin: auto;
}
.main-page__grid-item:last-child [data-content] h2 {
  font-size: 50px;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 16px;
}
.main-page__grid-item:last-child [data-content] p {
  font-size: 28px;
  font-weight: 300;
}
.main-page__grid-item:last-child [data-content] a {
  margin-top: 48px;
  margin-left: auto;
  width: max-content;
}
